export default {
  name: '',
  components: {
  },
  data () {
    return {
      count: null
    }
  },
  methods: {
    goIndex () {
      this.count = -1
      this.$router.push('/index')
    },
    countFun () {
      this.count = 5

      var timer = setInterval(() => {
        this.count--
        if (this.count < 0) {
          clearInterval(timer)
          this.$router.push('/index')
        }
      }, 1000)
    }
  },
  mounted () {
    document.getElementById('video').play()
    this.count = 0
    this.countFun()
  }
}



